import { FontIndex } from 'core/types/common';

/**
 * Возвращает название тэга для текста
 * @param {boolean} isInline признак строчного отображения текста
 * @param {fontIndex} [fontIndex] название тэга для надстрочного/подстрочного элемента
 * @returns {string}
 */
const getTextTagName = (isInline: boolean, fontIndex?: FontIndex): FontIndex | 'span' | 'p' => {
  if (fontIndex) {
    return fontIndex;
  }

  return isInline ? 'span' : 'p';
};

export default getTextTagName;
